import React from 'react';
import SignUp from './SignUp'
import LogIn from './LogIn'

import { Accordion, Card } from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import styled from 'styled-components'


const SCAccordion = styled(Accordion)`
/* background:red; */
margin:30px;
`
const SCAccordionT = styled(Accordion)`
/* background:red; */
/* margin:30px; */
&:hover{
  cursor:pointer;
  opacity:0.5;
}

transition: opacity 0.3s;
`

const SignUpInOut = ({setUser}) => {
  return (
    <div>
      <Accordion as={SCAccordion} >
        <Card >
          <Accordion.Toggle as={Card.Header} as={SCAccordionT} eventKey="0" className="p-3 mb-2 bg-info text-white text-center btnOpacity">
            Sign-up
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="0">
            <Card.Body>
              <SignUp />
            </Card.Body>
          </Accordion.Collapse>
        </Card>
        <Card >
          <Accordion.Toggle as={Card.Header} eventKey="1" className="p-3 mb-2 bg-info text-white MMpointer  text-center">
            Log-in
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="1">
            <Card.Body>
              <LogIn setUser={setUser} />
            </Card.Body>
          </Accordion.Collapse>
        </Card>

        {/* <Card >
          <Accordion.Toggle as={Card.Header} eventKey="2" className="p-3 mb-2 bg-info text-white MMpointer  text-center">
            Log-out
      </Accordion.Toggle>
          <Accordion.Collapse eventKey="2">
            <Card.Body>Hello! I'm another body
        <p>May-be later.</p>
            </Card.Body>
          </Accordion.Collapse>
        </Card> */}
      </Accordion>

    </div>
  );
};

export default SignUpInOut;