import React, { useEffect, useState } from 'react';
import signInWithJWTCookie from './functions/signInWithJWTCookie';
import SignUpInOut from './SignUpInOut'
import postJsonData from '../../functions/postJsonData'
import { Button } from 'react-bootstrap';
import { urlRoot } from '../../globalState/globalVariables';



const UserStatus = () => {
  const [user, setUser] = useState({ message: 'No user logged in.' })
  
  useEffect(() => {
    const getUserData = async () => {
      let user = await signInWithJWTCookie();
      console.log("user--> ", user);
      setUser(user);
    };
    getUserData();
  }, [])

  const logOut =() => {
    let response = postJsonData(`${urlRoot}/user/logOut`, {});
    //refresh page now
  }

  return (
    <div >
<p style={{ textAlign: 'right', paddingRight: '5px' }}>user:
      {(user && user.email ) ?
      (<span style={{ color: 'green' }}> {user.email} <Button onClick={logOut}>Log-out</Button> </span>) : <span style={{ color: 'red' }}>"No user. Wait sec, try to refresh the page, Log-in or Sign-up."</span>}</p>
      {(!user || !user.email) &&
        (<div>
          {/* <p><button>Log-in</button> <button>Sign-in</button></p> */}
          <SignUpInOut setUser={setUser} />
        </div>)}
      {/* <SignUp /> */}
    </div>
  );
};

export default UserStatus;